.Banner {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.Wrap {
  width: 150px;
  height: 150px;
  overflow: hidden;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrap1 {
  position: absolute !important;
